import { Button as MuiButton, Grid } from '@mui/material'
import { makeStyles } from '@material-ui/core'
import { SidePanelItem } from '../../Molecules/SidePanelItem/SidePanelItem'
import Link from '@mui/material/Link';
import { useState } from 'react';


export type OptionType = {
  src: any;
  text:string;
  flag?:number;
};
export type sidePanelProps = {
  sidePanelItems: Array<OptionType>;
};

export const SidePanel = (props: sidePanelProps) => {

  const{sidePanelItems}=props

  const useStyles = makeStyles((theme) => ({
    Item: {
    },
 
  }))
  var prevIndex=0;
  function handleOnClick(item: OptionType, index: number):void
  {
    sidePanelItems[prevIndex].flag=0;
    item.flag=1;
    prevIndex=index
  }
  const classes = useStyles()
  return (
    <Grid
      container
      direction="column"
      spacing={5}
      style={{
        width:'100px',
        height:'704px',
      }}
    >
      
      {sidePanelItems.map((item: OptionType, index: number) => {
return(
    <Grid item style={{backgroundColor:item.flag?'#EEEBFF':'#FFFFFF'
  }}>
<Link href="#" underline="none" onClick={()=>handleOnClick(item,index)} >

<SidePanelItem iconUrl={item.src} classname={classes.Item} text={item.text}></SidePanelItem>
</Link>
</Grid>

)})}
    </Grid>
  )
}
